  margin: 0rem 2rem;
  transition: background-color 0.3s ease;
}
.headerchange.scrolled {
  background-color: rgba(60, 147, 195, 0.4);}

.saanjh.scrolled .project {
  padding-top: 50px; /* Adjust this value based on your header height */
}

.mainheader{
