@media screen and (max-width:900px) {
  .navigation{
    display: none;
  }
  .newthing{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .symbol{
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin: 0rem 1rem;
  }
  .navigation.show{
    display: block;
    width: 100%;
    margin-bottom: 1.5rem;
  }
  .mainheader{
    flex-direction: column;
  }
  .navbar{
    flex-direction: column;
    gap: 2px;
    align-items: flex-start;
  }
  .navbar-elements{
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    margin: 0px;
    padding: 0px;
    gap: 2px;
  }
  .navbar-child{
    width: 100%;
  }
  .navbar-elements li{
    display: flex;
    margin: 0px;
    width: 100%;
    border: 2px solid blue;
  }
  .joinin{
    display: flex;
    width: 100%;
    border: 2px solid blue;
  }
  .joinin p{
    margin: 0px;
  }
}

