/* CSS for upload form */

.contparent{
  padding: 3% 2%;
}

.container {
  padding: 2% 2%;
  max-width: 900px;
  margin: 50px auto;
  
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.container h2 {
  text-align: center;
  color: #333;
}

.container form {
  display: flex;
  flex-direction: column;
}

.uploadform label {
  margin-bottom: 5px;
  color: #333;
}

.uploadform input[type="text"],
input[type="number"],
input[type="tel"],
input[type="email"],
select,
textarea {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
}

.uploadform input[type="file"] {
  margin-bottom: 15px;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

.buttons button {
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.buttons button[type="submit"] {
  background-color: #28a745;
  color: #fff;
}

.buttons button[type="reset"] {
  background-color: #dc3545;
  color: #fff;
}

.buttons button:hover {
  opacity: 0.9;
}


